import React from 'react';
import { makeStyles } from "@material-ui/core/styles";
import { auth } from "../auth/AuthContextProvider";
import { withRouter } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    spanLink: {
        color: theme.palette.primary.main,
        textDecoration: "underline",
        cursor: "pointer"
  }}));

const TestAccountLogin = ({history}) => {
    const classes = useStyles();

    const handleClick = () => {
        auth
        .signInWithEmailAndPassword("test@test.com", "123123123")
        .then(() => {
          history.push("/");
        })
    }
    
    return (
       <div>Click <span className={classes.spanLink} onClick={handleClick}>
       here
   </span > to login as testing account </div> 
    )
}

export default withRouter(TestAccountLogin)
